
export const titleCase = (str) =>  {
    str = str.toLowerCase();
    str = str.split(' ');
    for(let i = 0; i < str.length; i++) {
        str[i] = str[i].charAt(0).toUpperCase() + str[i].slice(1);
    }
    return str.join(' ')
}

export const joinString = (str) => {
    str = str.split(' ');
    for(let i = 0; i < str.length; i++) {
        str[i] = str[i];
    }
    return str.join('-')
}

export const convertUnixTime = (nr) => new Date(nr * 1000).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

export const currentTime = () => new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

