import React from 'react';
import './styles.css';
import { titleCase } from './utils.js';
import { convertUnixTime } from './utils';
import { currentTime } from './utils.js';
import dayjs from 'dayjs';

const language = window.navigator.userLanguage || window.navigator.language;
const langSliced = language.slice(0, 2);

require(`dayjs/locale/${langSliced}`);


const CardExampleCard = ({weatherData, cityData}) => (
    <div id="main" >
        <div className='header'>
            <div className='city'>
                <i className="fa-solid fa-location-dot"></i><span>{cityData.locality}</span>
            </div>
            <div className='date'>
                <span className='text-capitalize'>{dayjs().locale(`${langSliced}`).format('dddd DD MMMM')}</span>
            </div>
        </div> 
        <div className='weather-icon'>
            <img src={(`./icons/animated/${weatherData.current.weather[0].id === 800 && currentTime() < convertUnixTime(weatherData.current.sunset) ? 'clear-day' : weatherData.current.weather[0].id === 800 && currentTime() > convertUnixTime(weatherData.current.sunset) ? 'clear-night' : weatherData.current.weather[0].id}.svg`)} alt={weatherData.current.weather[0].description} />
            
        </div>
        <div className='description'>
            <p>{titleCase(weatherData.current.weather[0].description)}</p> 
        </div>
        <div className='temperature'>
            <span className='temperature-value'>{Math.floor(weatherData.current.temp)}<span className='degrees'>&deg;C</span></span>
        </div>
        <div className='min-max-temperature'>
            <span><i className="fa-solid fa-temperature-arrow-up"></i> {Math.floor(weatherData.daily[0].temp.max)}&deg; </span>
            <span><i className="fa-solid fa-temperature-arrow-down"></i> {Math.floor(weatherData.daily[0].temp.min)}&deg;</span>
        </div>
        <div className='humidity'>
            <span><i className="fas fa-tint"></i> {weatherData.current.humidity}%</span>    
        </div>


        
       
        <div className='sun-position'>
            <div className='sunrise'>
                <img className='sunrise-sun' src={('./icons/animated/clear-day.svg')} alt="" />
            </div>
            <span>{convertUnixTime(weatherData.current.sunrise)}</span>
            <div className='sunset'>
                <img className='sunset-moon' src={('./icons/animated/clear-night.svg')} alt="" />
            </div>
            <span>{convertUnixTime(weatherData.current.sunset)}</span>
        </div>
        
    </div>
)

export default CardExampleCard;


