    import './App.css';
    import React, { useEffect, useState } from "react";
    import Weather from './components/weather.js';
    import { Dimmer } from 'semantic-ui-react';
    import PropagateLoader from 'react-spinners/PropagateLoader';
    const language = window.navigator.userLanguage || window.navigator.language;
    const langSliced = language.slice(0, 2);
    function App() {
      
      const [lat, setLat] = useState([]);
      const [long, setLong] = useState([]);
      const [data, setData] = useState([]);
      const [city, setCity] = useState([]);
      
      /* get latitude and longitude */
      navigator.geolocation.getCurrentPosition(function(position) {
        setLat(position.coords.latitude);
        setLong(position.coords.longitude);
      });
      
      useEffect(() => {
        const fetchWeatherData = async () => {
          /* get weather datas */
          try {
            await fetch(`${process.env.REACT_APP_API_WEATHER_URL}?lat=${lat}&lon=${long}&units=metric&lang=${langSliced}&APPID=${process.env.REACT_APP_API_KEY}`)
            .then(res => res.json())
            .then(result => {
              setData(result)
            })

          } catch(err) {
            console.error(err)
          }
        }
        const reverseGeolocation = async () => {
          /* reverse geocoding and getting the exact city with bigdatacloud.com */
          try {
            await fetch(`${process.env.REACT_APP_API_REVERSE_GEOCODING_URL}?latitude=${lat}&longitude=${long}&localityLanguage=${navigator.language.split('-')[0]}`)
            .then(res => res.json())
            .then(result => {
              setCity(result)
            })
          } catch(err) {
            console.error(err)
          }
        }
        fetchWeatherData()
        reverseGeolocation()
      }, [lat, long])

      return (
      <div className="App">
        {/* since the fetch data is an async function, it's mandatory to include this check */}
        {(typeof data.current != 'undefined') ? 
          (<Weather weatherData={data} cityData={city}/>) : 
          (<div>
            <Dimmer active>
              <PropagateLoader
                color="#f5f5f5"
                cssOverride={{}}
                loading
                size={15}
                speedMultiplier={1}
              />
            </Dimmer>
          </div>)
        }
      </div>
    );
    }

export default App;
